body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#multiselect::-webkit-calendar-picker-indicator { display: none !important; }

.container {
  width: 60%;
  margin: 0 auto;
}

nav ul li {
  display: inline-block;
  width: 120px;
  float: right;
}


.form-group {
  display: block;
  margin: 8px 0;
}

.add-button {
  display: inline-block;
}

.x-btn {
  display: grid;
  align-items: center;
  width: 22px;
  margin-left: 24px;
}

nav {
  height: 84px;
  background-color: #10654e;
  margin-bottom: 100px;
}

.grid-1-1 {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.flex-01 {
  display: flex;
  color: darkblue;
}

.flex {
  display: flex;
}

.error-msg {
  color: #cc0000;
}







